export default {
    data() {
        return {

            data: [],

            // 
            filterIndex: "-1",

            // 
            filterShow: false,

            //
            filterSelect: {
            }
        }
    },
    // filterData:()   currenSelect:()
    props: ['filterData', 'currenSelect'],
    mounted() {
        let total = this.filterData;
        if (total) {
            this.data = this.filterData;
            // 
            total.forEach(element => {
                let key = element['key'];
                console.log(key);
                let value = element['options'] ? element['options'][0]["key"]: "";
                this.filterSelect[key] = value;
            });
        }
    },
    methods: {
        // 
        itemClicked(k) {
            this.filterIndex = k;
            if (!this.filterShow) {
                this.filterShow = true;
            }
        },

        // 
        selectItem(k, kk) {
            this.filterSelect[k] = kk;
            this.$emit("select", this.filterSelect);
            this.filterShow = false;
            this.filterIndex = "-1";
        }

    }
}
